import { toggleButtonGroupClasses } from '@mui/material';
import { height, hexToRgb, width } from '@mui/system';
import React from 'react';
import * as THREE from 'three';
import { Clock } from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
import { FBXLoader  } from 'three/examples/jsm/loaders/FBXLoader';

class LoadingBar extends React.Component {
    constructor(props) {
        super(props);
        this.windowwidth = window.innerWidth;
            this.windowheight = window.innerHeight;
            this.renderer =null;
            this.scene = null;
            this.camera =null;
            this.cube=null;
            this.Logo = null;
            this.light = null;
            this.controls = null;
            this.mixer = null;
            this.clock = null;
            this.animate=  this.animate.bind(this);
    }

    componentDidMount() {
        this.clock = new THREE.Clock();
        var myCanvas = document.getElementById("webglCanvas")

        this.scene  = new THREE.Scene();

        this.camera= new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 1000);

        this.renderer = new THREE.WebGLRenderer({ antialias: true, canvas: myCanvas });

        this.renderer.setSize(window.innerWidth, window.innerHeight);

        document.body.appendChild(this.renderer.domElement);

        this.controls = new OrbitControls(this.camera, this.renderer.domElement);

        this.camera.position.set(0,0,100);

        this.controls.update();

        var geometry = new THREE.BoxGeometry(1, 1, 1);

        var material = new THREE.MeshBasicMaterial({ color: 0x00ff00 });

        this.Logo = new GLTFLoader();
        let _this = this;
        //this.Logo.load('/Models/pencil.glb', function(gltf) {
        this.Logo.load('/Logo2.glb', function(gltf) {
            //gltf.scale.set(0.1,0.1,0.1);
            gltf.scene.scale.set(5, 5, 5);
            gltf.scene.position.x = 5;
            gltf.scene.position.y = 5;
            gltf.scene.position.z = 5;
            _this.scene.add(gltf.scene);

            // _this.mixer = new THREE.AnimationMixer(gltf);
            // for (var i = 0; i < gltf.animations.length; i++){
            // _this.mixer.clipAction(gltf.animations[i]).play();
            // }
            _this.animate();
        }, undefined, function(error) {
            console.error(error);
        })

        this.cube = new THREE.Mesh(geometry, material);

        //this.scene.add(this.cube);

        this.camera.position.z = 5;
        
        this.renderer.setSize(this.windowwidth, this.windowheight);
        this.renderer.setClearColor(0xDDDDDD, 1);
        document.body.appendChild(this.renderer.domElement);

        this.camera.position.z = 50;
        this.scene.add(this.camera);

        const color = 0xFFFFFF;
        const intensity = 1;
        this.light = new THREE.DirectionalLight( 0xFFFFFF, 0.5 );
        // this.light.color.setHSL( 0.1, 1, 0.95 );
        this.light.position.set(0, 0, 1);
        this.light.position.multiplyScalar( 100 );
        this.scene.add(this.light);
        // this.scene.add(this.light.target);
   
    }

    animate  () {

        requestAnimationFrame(this.animate);
        // this.cube.rotation.x += 0.01;

        // this.cube.rotation.y += 0.01;
        const delta = this.clock.getDelta();
        if (this.mixer){
        this.mixer.update(delta);
        }
        this.controls.update();
        this.renderer.render(this.scene, this.camera);

    };

    render() {
        return (
            <canvas id='webglCanvas'></canvas>
        )
    }
}
export default LoadingBar;

