import React from "react";
import '../ProfileShoot/ProfileShoot.css';
import Box from '@mui/material/Box';
import Slider from '@mui/material/Slider';
import InputSlider from '../../Controls/InputSlider';
import { red } from "@mui/material/colors";
import WebcamCapture from '../WebCam';
import Webcam from 'react-webcam';
import { LoadingBar } from '../LoadingBar';

class ProfileShoot extends React.Component {
    constructor(props) {
        super(props);
        this.state={
            height:165,
            weight:45,
            hairStyle:'custom',
            imagePath : '../icons/profile.png',
        }
        this.webcamRef=React.createRef();
        this.uploadImage = this.uploadImage.bind(this);
    }

    uploadImage(){
        console.log("Upload Image");
        document.getElementById("chooseImage").click();
        this.setState({isCameraOn : false});
    }
 
    captureImage(){
        const imageSrc = this.webcamRef.current.getScreenshot({width: 1920, height: 1080});
        console.log(imageSrc);
        this.setState({isCameraOn : false});
        this.props.setImageFile(imageSrc);
    }

    render() {
        const videoConstraints = {
            width: 200,
            height: 200,
            facingMode: "user"
          };
        return (
            <div  className="" id="addbranchmodal"  role="dialog" aria-labelledby="addbranchmodal" >
                <form  style={{ position: "absolute", width : this.props.isMobile === true ? "100%" : "25%", right : this.props.isMobile === true ? "0%" : "5%", top: this.props.isMobile === true ? "0%" : "5%", backgroundColor: this.props.isMobile === true ? "#1A1C3E" : "transparent"}} id="subscribe-email-form" className="form-horizontal formborder" role="form" method="post">
                    <div className="" role="document">
                        <div className="modal-form">
                            <div>
                                <div className="form-header">
                                    <h6 className="titlebar" id="exampleModalCenterTitle">Oyna Profile Shoot - SAREE</h6>
                                    <button type="button" className="close" data-dismiss="modal" aria-label="Close" onClick={()=>this.props.close()}>
                                        <h5 className="closetitle"> x</h5>
                                    </button>
                                </div>
                                <div className="modal-body">
                                    <div className="container">
                                        <div style={{ display: "flex", justifyContent: "center" }}>
                                  {this.state.isCameraOn ? (
                                      <Webcam audio={false}
                                              id="videoid"
                                              ref={this.webcamRef}
                                              videoConstraints={videoConstraints}
                                              style = {{  clipPath: `circle(50% at 50% 50%)`, width: "200px", height:"200px",transform: 'translateX(0%) translateY(0%)'}}/>
                                  ) : (<img style = {{  clipPath: `circle(50% at 50% 50%)`, width: "200px", height:"200px",transform: 'translateX(0%) translateY(0%)'}} src={ this.props.imageFile?this.props.imageFile:'../icons/profile.png' } id = "uploadimg" name="uploadimg"/>)}  
                                        </div>
                                        
                                        <div className="defaulticon">

                                        <input style={{visibility:'hidden',display:'none'}} id="chooseImage" type="file" accept="image/*"  onChange={ this.props.showFile } />
                                        <img src="../icons/folder.png" className="col-3" style = {{ cursor:"pointer" }} onClick={()=> this.uploadImage() } /> 
                                            <img src="../icons/webcam.png" className="col-3" style = {{ cursor:"pointer" }} onClick={()=>{
                                                if (this.state.isCameraOn){
                                                    this.captureImage()
                                                    this.setState({ isCameraOn: false });
                                                }
                                                else {this.setState({ isCameraOn: true });
                                            }
                                            }} />
                                            <img src="../icons/submit.png" className="col-3" style = {{ cursor:"pointer" }} onClick={()=>this.props.proceedGenerate( this.state)} ></img>
                                        </div>
                                        <div className="defaulticon">
                                            <InputSlider value={this.state.weight} setValue={(val)=>this.setState({weight:val})} Min={45} Max={100} label="Weight" />
                                        
                                            <InputSlider value={this.state.height} setValue={(val)=>this.setState({height:val})} Min={140} Max={200} label="Height" />

                                        </div>
                                        <h6 className="titlebar" id="exampleModalCenterTitle" style = {{ marginTop:"2%" }}>HairStyle</h6>
                                        <div className ="bottombuttons">
                                            <div className = "selectbtn" id="custom" name = "custom" style={{borderColor:this.state.hairStyle==='custom'?"green": "#F14FC5", width: this.props.isMobile === true ? '70px' : '100px', height: this.props.isMobile === true ? '70px' : '100px'}} onClick = { () => this.setState({hairStyle:"custom"}) }>
                                                <img src="../icons/hair1.png" ></img>
                                            </div>
                                            <div className = "selectbtn" id="ponytail_with_bangs" name = "ponytail_with_bangs" style={{borderColor:this.state.hairStyle==='ponytail_with_bangs'?"green": "#F14FC5", width: this.props.isMobile === true ? '70px' : '100px', height: this.props.isMobile === true ? '70px' : '100px'}} onClick = { () => this.setState({hairStyle:"ponytail_with_bangs"})  }>
                                                <img src="../icons/hair2.png" ></img>
                                            </div>
                                            <div className = "selectbtn" id = "long_wavy" name="long_wavy" style={{borderColor:this.state.hairStyle==='long_wavy'?"green": "#F14FC5", width: this.props.isMobile === true ? '70px' : '100px', height: this.props.isMobile === true ? '70px' : '100px'}} onClick = { () =>  this.setState({hairStyle:"long_wavy"}) }>
                                                <img src="../icons/hair3.png" ></img>
                                            </div>
                                        </div>

                                        {/* <div class="bottombuttons">
                                            <input className="checkbox-tools" type="radio" name="tools" id="tool-1" />
                                            <label className="for-checkbox-tools" for="tool-1">
                                                
                                                <img src="../icons/hair1.png" className="col-3"></img>
                                            </label>
                                            <input className="checkbox-tools" type="radio" name="tools" id="tool-2" />
                                            <label className="for-checkbox-tools" for="tool-2">
                                                <i className='uil uil-vector-square'><img src="../icons/hair2.png" className="col-3"></img></i>
                                            </label>
                                            <input className="checkbox-tools" type="radio" name="tools" id="tool-3" />
                                            <label className="for-checkbox-tools" for="tool-3">
                                                <i className='uil uil-vector-square'><img src="../icons/hair3.png" className="col-3"></img></i>
                                            </label>
                                            </div>*/}
                                        </div> 

                                </div>
                                {/* <div className="modal-footer">
                                    <button type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
                                    <button type="submit" className="btn btn-primary">Submit</button>
                                </div> */}
                            </div>
                        </div>
                    </div>
                </form>

            </div>
        )
    }
}

export default ProfileShoot;

