/**
 * @module Route
 */
import React from "react";
import { BrowserRouter, Switch, Route } from "react-router-dom";

import SettingsPage from "./settings";
import AllBranches from "./allbranches";
import BranchPage from "./branch";
import UserPage from "./Users";
import MembersPage from "./members";
import Files from "./files";
import Logout from "./logout";
import AboutPage from "./about";
import TeamsPage from "./teams";
import ProcessLog from "./processlog";
import HomePage from "./home";
import OynaViewer from "../oynaviewer/oynaviewer"
import LoadingBar from "../Modal/LoadingBar";
/**
* @author Muthu G & Balaji K
* @description Routes for the react pages

*/
const Routes = (props) => {
  //  console.log("react");
   // localStorage.clear();
  
  return (
    <BrowserRouter>
      <Switch>
        <Route path="/Branches/Users">
          <MembersPage />
        </Route>
        <Route path="/Branches/Files" component={Files}></Route>
        <Route path="/Branches/AllBranches">
          <AllBranches />
        </Route>
        <Route path="/about">
          <AboutPage />
        </Route>
        <Route path="/another/teams">
          <TeamsPage />
        </Route>
        <Route path="/another/processmessage">
          <ProcessLog />
        </Route>
        <Route path="/settings">
          <SettingsPage />
        </Route>
        <Route path="/client">
          <HomePage />
        </Route>
        <Route path="/logout" component={Logout}></Route>
        <Route path="/branch/:id" component={BranchPage}></Route>

        <Route path="/users/:id" component={UserPage}></Route>
        <Route path="/oynaviewer"   >
          <OynaViewer { ...props }></OynaViewer>
        </Route>
        <Route path="/oynaviewerNormal"   >
          <OynaViewer normalLoad { ...props }></OynaViewer>
        </Route>
        <Route path="/loadbar"   >
          <LoadingBar ></LoadingBar>
        </Route>
        
        <Route path="/"  >
        <OynaViewer { ...props }></OynaViewer>
        </Route>
      </Switch>
    </BrowserRouter>
  );
};

export default Routes;
