// axios.defaults.baseURL =  process.env.REACT_APP_SERVER_HOST;
class OynaViewer extends React.Component {
  constructor(props) {
    super(props);
    console.log(props)
    this.state = {
      collapsebtn: true,
      isplay: false,
      reqframe: false,
      ModalShow: false,
      init: false,
      file: null,
    };
    this.gl = null;
    this.canvas = null;
    this.width = null;
    this.height = null;
    this.camera = null;
    this.shaderNormal = null;
    this.renderbuffer = null;
    this.fboPicking = null;
    this.PostionText = null;
    this.FragTexture = null;
    this.Mannequins = {}
    this.Textures = {};
    this.collapsebtn = true;
    this.logobtn = null;
    this.closebtn = null;
    this.DoRender = true;
    this.LFAPose = "LFA"
    this.LAOPose = "LAO"
    this.CurrntPose = null;
    this.assigned_var_3 = null;
    this.curSareeGetted = false;
    this.timePassed = 0;
    this.timeLeft = 0;
    this.timerInterval = null;
    this.reqframe = true;
    this.oynaPanel = document.getElementById('oyna_panel');
    this.serverURL =  process.env.REACT_APP_SERVER_IP;
    this.urlicon = this.serverURL + "Icons/";
    this.HostUrl=  process.env.REACT_APP_SERVER_HOST;
    this.mannq = [];
    this.Buttons = [];
    this.canvasRef = React.createRef();
  }


  componentWillMount() {
    console.log("will mount");
  }
  async Init() {

    console.log("INIT", window, document.body.clientWidth, document.body.clientHeight);
    this.canvas = document.getElementById("webglcanvas");
    this.canvas.width = document.body.clientWidth > 0 ? document.body.clientWidth : this.state.sareetextures.width; //document.width is obsolete
    this.canvas.height = document.body.clientHeight > 0 ? document.body.clientHeight : this.state.sareetextures.height; //document.height is obsolete
    this.width = this.canvas.width;
    this.height = this.canvas.height;
    this.gl = this.canvas.getContext("webgl2", { alpha: false, antialias: true });
    if (!this.gl) {
      console.error("WebGL 2 not available");
      document.body.innerHTML = "This Module requires WebGL 2 which is unavailable on this system."
    }
    this.gl.getExtension("EXT_color_buffer_float");
    this.gl.viewport(0, 0, this.width, this.height);
    this.gl.enable(this.gl.DEPTH_TEST);
    this.gl.enable(this.gl.BLEND);
    this.camera = new Camera(this.width / this.height);

    this.shaderNormal = new Shader("Normal Rendering", shader_vs, shader_fs);
    this.shaderNormal.compileShader(this.gl);
    this.shaderNormal.bind(this.gl);
    this.shaderNormal.setUnifromLocations(this.camera, this.gl);

    window.addEventListener('resize', this.resizeEvent);
    console.log("cache");
    let cachedata = await localForage.getItem('Model');
    if (cachedata) {
      this.LoadModel(cachedata, this.LFAPose);

    } else {
      let _this = this;
      new JSZip.external.Promise(function (resolve, reject) {
        JSZipUtils.getBinaryContent(_this.serverURL + 'Models/lao.zip', function (err, data) {
          if (err) {
            reject(err);
          } else {
            resolve(data);
          }
        });
      }).then(function (data) {
        return JSZip.loadAsync(data);
      })
        .then(function (zip) {
          return zip.file("lao.txt").async("string");
        }).then(function success(text) {
          _this.LoadModel(text, _this.LFAPose);
          localForage.setItem('Model', text);
        },
          function error(e) {
          });
    }

    let Real_Model = await localForage.getItem('Real_Model');
    if (Real_Model) {
      this.LoadModel(Real_Model, this.LAOPose);
    }
    this.InitWindow();
  }

  componentDidMount() {
    let _this = this;
    if (this.props.normalLoad) {
      this.Init();
    }
    else {
      window.addEventListener('message', function (event) {
        //var origin = event.origin || event.originalEvent.origin; // For Chrome, the origin property is in the event.originalEvent object.
        // if (origin !== /*the container's domain url*/)
        //     return;
        if (event.data.call == 'sendValue') {
          console.log("send value", event);
          if (!_this.state.sareetextures) {
            _this.setState({ sareetextures: event.data });
            _this.Init();
          }

        }
      }, false);
    }



    // const obj = ReactDOM.findDOMNode(this);
    // obj.style.height = obj.contentWindow.document.body.scrollHeight + 'px';
  }

  setTextures = (data) => {
    let { gl, CurrntPose, Textures, textures } = this;
    // textures = Textures[0];
    Textures["SAREE"].BindTexture(gl, data.sareeImg);
    Textures["PALLU"].BindTexture(gl, data.palluImg);
    Textures["BLOUSE"].BindTexture(gl, data.blouseImg);
  }
  ChangetoCurrent = () => {
    const { gl, CurrntPose, Textures, textures } = this;

    var sareeImg = document.getElementById('saree-image-value').value;
    var palluImg = document.getElementById('pallu-image-value').value;
    var blouseImg = document.getElementById('blouse-image-value').value;
    if (sareeImg && sareeImg !== "" && palluImg && palluImg !== "" && blouseImg && blouseImg !== "") {
      // textures = Textures[0];
      Textures["SAREE"].BindTexture(gl, sareeImg);
      Textures["PALLU"].BindTexture(gl, palluImg);
      Textures["BLOUSE"].BindTexture(gl, blouseImg);
      // for (var i = 0; i < textures.length; i++) {
      //   if (textures[i].Name.toUpperCase() === "SAREE") {
      //     textures[i].BindTexture(gl, sareeImg);
      //   }
      //   if (textures[i].Name.toUpperCase() === "PALLU") {
      //     textures[i].BindTexture(gl, palluImg);
      //   }
      //   if (textures[i].Name.toUpperCase() === "BLOUSE") {
      //     textures[i].BindTexture(gl, blouseImg);
      //   }
      // }
      this.setState({ curSareeGetted: true })

      document.getElementById('saree-image-value').value = "";
      document.getElementById('pallu-image-value').value = "";
      document.getElementById('blouse-image-value').value = "";
    }
  }
  resizeEvent = () => {
    this.gl.viewport(0, 0, this.canvas.width, this.canvas.height);
    // this.setState({ width: window.innerWidth, height: window.innerHeight });
  };
  
  ColorToImg(colval) {
    var canvas2 = document.createElement("canvas");
    var context2 = canvas2.getContext('2d');
    canvas2.width = 400;
    canvas2.height = 400;
    var r_a = 0.8;
    context2.fillStyle = "rgba(" + colval[0] * 255 + "," + colval[1] * 255 + "," + colval[2] * 255 + ", " + r_a + ")";
    context2.fillRect(0, 0, canvas2.width, canvas2.height);
    return canvas2.toDataURL();
  }

  playpause() {
    {
      if (this.state.isplay) {

        clearInterval(this.timerInterval);
        this.setState({ reqframe: true });
        this.draw();

      }
      else {

        this.setState({ reqframe: true });
        this.startTimer();
      }
      this.setState({ isplay: !this.state.isplay });
    }
  }
  LoadModel(srctext, curpose) {

    const { gl, LFAPose, urlicon, timerInterval } = this;
    const { collapsebtn, isplay, reqframe } = this.state;
    let modelInfo = JSON.parse(srctext);
    this.CurrntPose = curpose;

    this.mannq = [];
    this.Buttons = [];
    let _this = this;
    for (var i = 0; i < modelInfo.meshes.length; i++) {
      let mesh = new Meshes(modelInfo.meshes[i].vertices, modelInfo.meshes[i].Faces, modelInfo.meshes[i].IsColor, gl);
      mesh.Name = modelInfo.meshes[i].Name;
      mesh.IsAlphaMap = modelInfo.meshes[i].IsAlphaMap;
      this.mannq.push(mesh);
    }
    this.Mannequins[curpose] = this.mannq;
    // if(this.Textures.length>0)
    // this.textures={...this.Textures};
    // else
    // this.textures = {};
    for (var i = 0; i < modelInfo.Textures.length; i++) {

      var textu = new Texture(modelInfo.Textures[i].Name, gl);
      if (!(textu.Name.toUpperCase() === "SAREE" || textu.Name.toUpperCase() === "PALLU" || textu.Name.toUpperCase() === "BLOUSE")) {
        textu.BindTexture(gl, 'data:image/' + modelInfo.Textures[i].Extention + ';base64,' + modelInfo.Textures[i].ImgByte);

      }

      this.Textures[modelInfo.Textures[i].Name] = textu;
    }
    // this.Textures[0] = this.textures;

  }
  intEvents = () => {
    const { gl, canvas, camera } = this;
    canvas.addEventListener("touchstart", handleStart, false);
    canvas.addEventListener("touchend", handleEnd, false);
    //canvas.addEventListener("touchcancel", handleCancel, false);
    canvas.addEventListener("touchmove", handleMove, false);
    let _this = this;
    function handleMove(e) {

      camera.TouchMove(e);
    }

    function handleStart(e) {
      camera.TouchStart(e);
      // if (e.touches.length >= 1) {
      //   _this.ClickOnScreen(vec2.fromValues(e.touches[0].pageX, e.touches[0].pageY));

      // }
    }

    function handleEnd(e) {
      camera.TouchEnd(e);
    }

    canvas.onwheel = function (e) {
      camera.MouseWheel(e);

    }

    canvas.onmousedown = function (e) {
      e.preventDefault();
      camera.prevLocation = vec2.fromValues(e.x, e.y);
      // if (e.buttons == 1) {
      //   _this.setState({DoRender:false})
      //   _this.ClickOnScreen(vec2.fromValues(e.x, e.y));
      //   _this.setState({DoRender:true})
      //   _this.draw() ;
      // }
    }

    canvas.onmousemove = function (e) {
      if (camera.prevLocation[0] === 0 && camera.prevLocation[1] === 0) return;
      camera.MouseMove(e);
    }
  }
  changeskintone = () => {
    this.mannq = this.Mannequins[this.CurrntPose];
    var skintoneval = vec3.fromValues(this.pixelvalue[0], this.pixelvalue[1], this.pixelvalue[2]);

    for (var i = 0; i < this.mannq.length; i++) {
      if (this.mannq[i].Name.toUpperCase().includes("BODY"))
        this.mannq[i].gDirectionalLightbasecolor = skintoneval;
    }
    this.Mannequins[this.CurrntPose] = this.mannq;
  }

  startTimer = () => {
    this.timerInterval = setInterval(() => {
      this.camera.yawAngle += 0.03;

      this.draw();

    }, 100);
  }
  ClickOnScreen = (postion) => {
    const { gl, canvas, camera, fboPicking, shaderNormal, sphere, Buttons, shaderColor, logobtn, closebtn } = this;
    // let width=canvas.width/2;
    // let height=canvas.height/2;
    // gl.viewport(0, 0, this.width, this.height);

    gl.bindFramebuffer(gl.FRAMEBUFFER, fboPicking);
    gl.drawBuffers([gl.COLOR_ATTACHMENT0, gl.COLOR_ATTACHMENT1]);

    gl.clearColor(0, 0, 0, 0);

    // Clear the canvas before we start drawing on it.
    gl.clear(gl.COLOR_BUFFER_BIT | gl.DEPTH_BUFFER_BIT | gl.STENCIL_BUFFER_BIT);
    shaderNormal.bind(gl);
    for (var i = 0; i < Buttons.length; i++) {
      this.buttonClick(Buttons[i], postion);
    }
    if (logobtn.eventClick != null) {
      this.buttonClick(logobtn, postion);
    }
    if (closebtn.eventClick != null) {
      this.buttonClick(closebtn, postion);
    }
    if (sphere != null && sphere.eventClick != null) {
      gl.clear(gl.COLOR_BUFFER_BIT | gl.DEPTH_BUFFER_BIT | gl.STENCIL_BUFFER_BIT);
      shaderColor.setUnifromLocations(camera, gl);
      shaderColor.EnableButtonLight(gl);
      shaderColor.bind(gl);
      sphere.Update(camera, gl, shaderColor);

      gl.readBuffer(gl.COLOR_ATTACHMENT0);
      var val = new Float32Array(4);
      gl.readPixels(postion[0], (this.width - postion[1]), 1, 1, gl.RGBA, gl.FLOAT, val);
      if (val[3] !== 100)
        sphere.eventClick(val);
    }
    // canvas.width=canvas.width*2;
    // canvas.height=canvas.height*2;
    // gl.viewport(0, 0, canvas.width, canvas.height);

    gl.bindFramebuffer(gl.FRAMEBUFFER, null);
  }
  buttonClick = (btn, postion) => {
    const { gl, canvas, camera, fboPicking, shaderNormal } = this;

    gl.clear(gl.COLOR_BUFFER_BIT | gl.DEPTH_BUFFER_BIT | gl.STENCIL_BUFFER_BIT);
    btn.Update(camera, gl, shaderNormal);
    gl.bindFramebuffer(gl.READ_FRAMEBUFFER, fboPicking);
    gl.readBuffer(gl.COLOR_ATTACHMENT1);
    var val = new Float32Array(4);
    gl.readPixels(postion[0], ((canvas.height) - (postion[1])), 1, 1, gl.RGBA, gl.FLOAT, val);

    if (val[3] !== 0 && btn.eventClick !== null) {
      btn.eventClick();
    }

  }
  draw = () => {
    const { gl, Mannequins, camera, shaderNormal, CurrntPose, Buttons, Textures, logobtn, closebtn } = this;
    const { DoRender, curSareeGetted, reqframe, collapsebtn } = this.state;
    if (!DoRender) return;
    if (!curSareeGetted) {

      requestAnimationFrame(this.draw);
      return;
    }

    gl.clear(gl.COLOR_BUFFER_BIT | gl.DEPTH_BUFFER_BIT);


    gl.clearColor(20 / 255, 26 / 255, 62 / 255, 255 / 255);
    gl.enable(gl.DEPTH_TEST);
    gl.enable(gl.BLEND);
    gl.blendFunc(gl.SRC_ALPHA, gl.ONE_MINUS_SRC_ALPHA);

    shaderNormal.setUnifromLocations(camera, gl);
    shaderNormal.bind(gl);
    this.mannq = Mannequins[CurrntPose];
    // this.textures = Textures[0];
    if(  this.mannq){
      for (var i = 0; i < this.mannq.length; i++) {
        this.Textures[this.mannq[i].Name].Use(gl);
        if (this.mannq[i].IsAlphaMap) {
          this.Textures["alpha" + this.mannq[i].Name].Use2(gl);
          var isalphamap = shaderNormal.getUniformLoc('isalphamap');
          gl.uniform1i(isalphamap, 1);
          var alphamap = shaderNormal.getUniformLoc('alphamap');
          gl.uniform1i(alphamap, 1);
        }
        else {
          var isalphamap = shaderNormal.getUniformLoc('isalphamap');
          gl.uniform1i(isalphamap, 0);
        }
  
        this.mannq[i].Update(camera, gl, shaderNormal);
      }
    }
   
    var isalphamap = shaderNormal.getUniformLoc('isalphamap');
    gl.uniform1i(isalphamap, 0);
    // shaderNormal.EnableButtonLight(gl);
    // closebtn.Update(camera, gl, shaderNormal);
    // if (!collapsebtn) {
    //   for (var i = 0; i < Buttons.length; i++) {
    //     Buttons[i].Update(camera, gl, shaderNormal);
    //   }
    //   logobtn.Update(camera, gl, shaderNormal);
    // }
    // else {
    //   logobtn.Update(camera, gl, shaderNormal);
    // }

    // shaderNormal.DisableButtonLight(gl);
    if (reqframe)
      requestAnimationFrame(this.draw);

  }

  initPickingBuffers = () => {
    const { gl, canvas, mannq, shaderNormal, CurrntPose, Buttons, Textures, logobtn, closebtn, textures, collapsebtn } = this;
    const { DoRender, curSareeGetted, reqframe } = this.state;
    var WindowWidth = canvas.width;
    var WindowHeight = canvas.height;

    this.renderbuffer = gl.createRenderbuffer();
    this.fboPicking = gl.createFramebuffer();


    this.PostionText = gl.createTexture();
    this.FragTexture = gl.createTexture();

    gl.bindFramebuffer(gl.FRAMEBUFFER, this.fboPicking);

    //texture for projected coordinates
    gl.activeTexture(gl.TEXTURE0);
    gl.bindTexture(gl.TEXTURE_2D, this.PostionText)
    gl.texImage2D(gl.TEXTURE_2D, 0, gl.RGBA32F, WindowWidth, WindowHeight, 0, gl.RGBA, gl.FLOAT, null);

    gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_WRAP_S, gl.REPEAT);
    gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_WRAP_T, gl.REPEAT);
    gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_MAG_FILTER, gl.NEAREST);
    gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_MIN_FILTER, gl.NEAREST);
    gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_BASE_LEVEL, 0);
    gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_MAX_LEVEL, 0);



    //texture for texture coordinates
    gl.activeTexture(gl.TEXTURE0);
    gl.bindTexture(gl.TEXTURE_2D, this.FragTexture);
    gl.texImage2D(gl.TEXTURE_2D, 0, gl.RGBA32F, WindowWidth, WindowHeight, 0, gl.RGBA, gl.FLOAT, null);

    gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_WRAP_S, gl.REPEAT);
    gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_WRAP_T, gl.REPEAT);
    gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_MAG_FILTER, gl.NEAREST);
    gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_MIN_FILTER, gl.NEAREST);
    gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_BASE_LEVEL, 0);
    gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_MAX_LEVEL, 0);




    gl.bindRenderbuffer(gl.RENDERBUFFER, this.renderbuffer);
    gl.renderbufferStorage(gl.RENDERBUFFER, gl.DEPTH_STENCIL, WindowWidth, WindowHeight);

    gl.framebufferTexture2D(gl.FRAMEBUFFER, gl.COLOR_ATTACHMENT0, gl.TEXTURE_2D, this.PostionText, 0);
    gl.framebufferTexture2D(gl.FRAMEBUFFER, gl.COLOR_ATTACHMENT1, gl.TEXTURE_2D, this.FragTexture, 0);
    gl.framebufferRenderbuffer(gl.FRAMEBUFFER, gl.DEPTH_STENCIL_ATTACHMENT, gl.RENDERBUFFER, this.renderbuffer);

    gl.bindRenderbuffer(gl.RENDERBUFFER, null);


    // Disable reading to avoid problems with older GPUs
    gl.readBuffer(gl.NONE);

    gl.drawBuffers([gl.COLOR_ATTACHMENT0, gl.COLOR_ATTACHMENT1]);

    // Verify that the FBO is correct
    var Status = gl.checkFramebufferStatus(gl.FRAMEBUFFER);

    if (Status !== gl.FRAMEBUFFER_COMPLETE) {
      console.log("Problem with framebuffer " + Status + ". Should be " + gl.FRAMEBUFFER_COMPLETE);
    }

    // Restore the default framebuffer
    gl.bindTexture(gl.TEXTURE_2D, null);
    gl.bindFramebuffer(gl.FRAMEBUFFER, null);
  }

  showFile = async (e) => {
    e.preventDefault();
    console.log("Show File");
    this.setState({ file: e.target.files[0] });

    const reader = new FileReader();
    reader.onload = async (e) => {
      const text = e.target.result;
      
      this.setState({ Imagefile: text });
    };
    if (e.target.files[0])
    reader.readAsDataURL(e.target.files[0]);
  };

  ProfileShot() {
    return (
      <div style={{ backgroundColor: "white", zIndex: 15, position: "absolute" }} id="addbranchmodal" role="dialog" aria-labelledby="addbranchmodal" aria-hidden="true">
        <div ><h6>This is New Feature</h6>
          <input type="file" onChange={(e) => this.showFile(e)} />
          <button type="button" class="btn btn-secondary" data-dismiss="modal" onClick={() => {

            this.setState({ showModal: false })
          }}>Close</button>
        </div>
      </div>


    )
  }
 
   dataURLtoFile(dataurl, filename) {
 
    var arr = dataurl.split(','),
        mime = arr[0].match(/:(.*?);/)[1],
        bstr = atob(arr[1]), 
        n = bstr.length, 
        u8arr = new Uint8Array(n);
        
    while(n--){
        u8arr[n] = bstr.charCodeAt(n);
    }
    
    return new File([u8arr], filename, {type:mime});
}
  async GetAvatar(filetext,params) {
    const formData = new FormData();
    console.log(filetext);
    const file =  this.dataURLtoFile(this.state.Imagefile,'profile.png')
    formData.append("avatarfile", file);
    formData.append("height", params.height);
    formData.append("weight", params.weight);
    formData.append("hairstyle", params.hairStyle);
    let _this = this;
    const config = {
      headers: {
        "content-type": "multipart/form-data",
        Authorization: JSON.parse(localStorage.getItem("token")).token,
      },
      responseType: 'arraybuffer',

      onUploadProgress: (progressEvent) => {
        console.log({
          progress: Math.round(
            (100 * progressEvent.loaded) / progressEvent.total
          )
        });
        this.setState({
          progress: Math.round(
            (100 * progressEvent.loaded) / progressEvent.total
          ),
        });
      },
    };
    axios
      .post( _this.HostUrl+"AvatarGenerator", formData, config)
      .then(async (response) => {
        console.log(response);

        JSZip.loadAsync(response.data).then(function (zip) {
          return zip.file("lao.txt").async("string")
        })
          .then(function (content) {
            console.log(content);
            // use content
            _this.LoadModel(content, _this.LAOPose);

            localForage.setItem('Real_Model', content);

          });

      });


  }
  InitWindow(){
    let _this=this;
    this.intEvents();
    if (this.state.sareetextures)
      this.setTextures(this.state.sareetextures);//       _this.setState({sareetextures:event.data});

    this.draw();
    setTimeout(function () {
      console.log("Changes");
      _this.canvas.style.display = "block";
      _this.setState({ DoRender: true, curSareeGetted: true, reqframe: true });
      _this.draw();
    }, 2000);
  }
  /**
  * @function
  * @author Muthu G & Balaji K
  * @name render
  * @description It will render the page
  */
  canvasClciked = () => {
    console.log("init event");
    if (!this.state.init) {
      this.setState({ init: true })
      this.Init();
    }
  }

  proceedGenerate(params){
    this.GetAvatar(this.state.file,params);
  }

  render() {
    const skintoneImage = {
      width: '100%',
      height: '100%',
      clipPath: `circle(80% at center)`
    }
    const skinBtns = [{
      img: <img src={dark1} onClick={() => {
        this.pixelvalue = vec3.fromValues(180 / 255, 140 / 255, 120 / 255);
        this.changeskintone();
      }} style={skintoneImage} />
    },
    {
      img: <img src={dusky1} onClick={() => {
        this.pixelvalue = vec3.fromValues(220 / 255, 180 / 255, 160 / 255);
        this.changeskintone();
      }} style={skintoneImage} />
    },
    {
      img: <img src={fair2} onClick={() => {
        this.pixelvalue = vec3.fromValues(240 / 255, 240 / 255, 240 / 255);
        this.changeskintone();
      }} style={skintoneImage} />
    },
    {
      img: <img src={fair1} onClick={() => {
        this.pixelvalue = vec3.fromValues(230 / 255, 210 / 255, 180 / 255);
        this.changeskintone();
      }} style={skintoneImage} />
    },
    ];
    const menuBtns = [{
      img:
      <div role="button" href="#0" onClick={() => {
        this.CurrntPose = this.LFAPose;
        this.draw();
        window.location = window.location.href.replace('#menu', '#0');
      }}  > <img src={LFA_POSEPng} style={skintoneImage} />
      </div>
    },
    {

      img: <div role="button" onClick={() => {
        this.CurrntPose = this.LAOPose;
        this.draw();
        window.location = window.location.href.replace('#menu', '#0');
      }}>
        <img src={this.state.Imagefile ? this.state.Imagefile : add1} style={skintoneImage} />
      </div>
    },
    {
      img:
        <div role="button" onClick={() => {
          this.setState({ showModal: true })
          window.location = window.location.href.replace('#menu', '#0');
        }} >
          <img role="button" src={add1} style={skintoneImage} />
        </div>

    },
    ];
    return (
      <div>
        <DashboardLayout>
          <div >
            <canvas className="canvas-container" style={{ height: "100%", position: "fixed", top: 0, left: 0, width: "100%" }} id="webglcanvas">
            </canvas>
            {!this.state.showModal && (
            <div style={{ right: 10, top: 20, position: "fixed", color: "white" }}>
              <button onClick={() => this.playpause()} >
              {this.state.isplay?(<img src={pause} alt="Avatar" style={{ width: "70px" }}></img>):(
                <img src={playpng} alt="Avatar" style={{ width: "70px" }}></img>
              )}
              </button>
            </div>
            )}
            {this.state.showModal&&(
              <div style = {{ zIndex : 20 }}>
                <ProfileShoot showFile={(e)=>this.showFile(e)}
                              imageFile= { this.state.Imagefile }
                              isMobile = { this.props.isMobile }
                              setImageFile = { (e) => this.setState({ Imagefile: e }) }
                              proceedGenerate={(val)=>this.proceedGenerate(val)}
                              close={()=>this.setState({showModal:false})}/>
              </div>
            )} 
            {!this.state.showModal && (
            <div style={{ right: 20, top: 160, zIndex: 15, position: "fixed", color: "white" }}>
              <CircleMenuBar name="models"
                             MenuButtons={menuBtns}
                             MenuIcon={<img src={girlpng} style={{
                             backgroundColor: "rgb(20,26,62)",
                             width: '100%',
                             height: '100%'}} />}
             />
            </div>
            )}
            {!this.state.showModal && (
            <div style={{ right: 20, top: 240, position: "fixed", color: "white" }}>
              <SkintoneMenu
                name="skintone"
                MenuButtons={skinBtns}
                MenuIcon={<img src={dark1} style={{
                  width: '100%',
                  height: '100%'
                }} />}/> 
              </div>
            )}
          </div>
        </DashboardLayout>
      </div>
    );
  }
}

export default OynaViewer;
